import { useSelector } from 'react-redux';
import { notDisplayBuiltinScriptNames } from '../constant';
/**
 * 隐藏logic所用的相关脚本
 * @returns 过滤后的脚本
 */
var useBuiltinScript = function () {
    var builtInScripts = useSelector(function (state) { return state.scriptState.builtInScripts.data; });
    var newBuiltInScripts = {};
    Object.keys(builtInScripts)
        .filter(function (item) { return !notDisplayBuiltinScriptNames.includes(item); })
        .forEach(function (item) {
        newBuiltInScripts[item] = builtInScripts[item];
    });
    return newBuiltInScripts;
};
export { useBuiltinScript };
