import { CustomisedScriptItem } from '@possie-engine/dr_front_surveyconfig';
import { v4 } from 'uuid';
/**
 * 检查script的唯一性
 */
var checkScriptsNameUnique = function (scriptsState, name) {
    var keys = Object.keys(scriptsState);
    return !keys.includes(name);
};
/**
 * 添加脚本
 * @param scriptName 自定义脚本名称
 * @returns 自定义脚本
 */
var getCustomScript = function (scriptName) {
    var id = v4();
    var kind = 'CustomisedScriptItem';
    var meta = { name: scriptName };
    var editor = null;
    var data = {
        function: '',
        arguments: [],
        information: '',
        return: null,
        status: 'draft',
    };
    return new CustomisedScriptItem(id, kind, meta, editor, data);
};
export { checkScriptsNameUnique, getCustomScript };
