/**
 *
 * @param classNames classnames
 * @returns class
 */
var getClassNames = function (classNames) {
    var cls = [];
    Object.keys(classNames).map(function (item) {
        if (classNames[item]) {
            cls.push(item);
        }
    });
    return cls.join(' ');
};
export default getClassNames;
