import { createOptionItem } from './create';
/**
 * 获取codes
 */
var handleGetCodes = function (options) {
    var codes = [];
    options.forEach(function (item) {
        codes.push(item.data.code);
    });
    return codes;
};
/**
 * 获取新code
 */
var handleNewCode = function (options) {
    var code = 0;
    var codes = handleGetCodes(options);
    while (codes.includes(String(code))) {
        code = code + 1;
    }
    return String(code);
};
/**
 * 创建option
 */
var createOption = function (options, languages) {
    var code = handleNewCode(options);
    return createOptionItem(code, languages);
};
/**
 * 验证code是否存在
 */
var validationCode = function (options) {
    var codes = handleGetCodes(options);
    return function (code) {
        return codes.includes(code);
    };
};
export { createOption, validationCode };
