/**
 * 十六进制转rgb
 */
var hexToRgb = function (str) {
    var value = str.replace('#', '');
    var rgb = [0, 0, 0];
    if (value.length === 6) {
        for (var i = 0; i < 3; i++) {
            rgb[i] = parseInt(value.slice(i * 2, i * 2 + 2), 16);
        }
    }
    else if (value.length === 3) {
        var arr = value.split('');
        for (var i = 0; i < 3; i++) {
            rgb[i] = parseInt("".concat(arr[i]).concat(arr[i]), 16);
        }
    }
    else {
        console.error("".concat(str, " failed!"));
    }
    return rgb.join(',');
};
export { hexToRgb };
