/**
 * 复制文本
 * @param str 要复制的文本
 */
var copyText = function (str) {
    var copyContainer = document.createElement('input');
    copyContainer.setAttribute('value', str);
    document.body.appendChild(copyContainer);
    copyContainer.select();
    document.execCommand('copy');
    document.body.removeChild(copyContainer);
};
export { copyText };
