var redirectToSignInPage = ''; //redirect to sign in page
var redirectToSignOutPage = ''; //redirect to sign out page
var redirectToQeditorDashboard = '';
var lang = ['zh-HK', 'zh-TW', 'zh-CN'].includes(navigator.language) ? 'cn' : 'en';
switch (process.env.NODE_ENV) {
    case 'development':
        redirectToSignInPage = "https://api.dev.datareachable.net/common/oidc/v2/entry?app=qeditor-v2-dev&lang=".concat(lang);
        redirectToSignOutPage = "https://api.dev.datareachable.net/common/oidc/v2/logout?app=qeditor-v2-dev";
        redirectToQeditorDashboard = 'https://qdashboard.dev.datareachable.net/v2/dev';
        break;
    case 'v2_dev':
        redirectToSignInPage = "https://api.dev.datareachable.net/common/oidc/v2/entry?app=qeditor-v2-dev&lang=".concat(lang);
        redirectToSignOutPage = "https://api.dev.datareachable.net/common/oidc/v2/logout?app=qeditor-v2-dev";
        redirectToQeditorDashboard = 'https://qdashboard.dev.datareachable.net/v2/dev';
        break;
    case 'v2_test':
        redirectToSignInPage = "https://api.dev.datareachable.net/common/oidc/v2/test/entry?app=qeditor-v2-test&lang=".concat(lang);
        redirectToSignOutPage = "https://api.dev.datareachable.net/common/oidc/v2/test/logout?app=qeditor-v2-test";
        redirectToQeditorDashboard = 'https://qdashboard.dev.datareachable.net/v2/test';
        break;
}
export { redirectToSignInPage, redirectToSignOutPage, redirectToQeditorDashboard };
