var withImages = function (editor) {
    var isVoid = editor.isVoid, isInline = editor.isInline, apply = editor.apply;
    /**
     * 设置节点为空
     * @param element
     * @returns
     */
    editor.isVoid = function (element) {
        return element.type === 'script' ? true : isVoid(element);
    };
    /**
     * 设置节点为行类
     * @param element
     * @returns
     */
    editor.isInline = function (element) {
        return element.type === 'script' ? true : isInline(element);
    };
    /**
     * 拦截
     * @param op
     */
    editor.apply = function (op) {
        apply(op);
    };
    return editor;
};
export default withImages;
