/**
 * 检查ReusableList的name是否唯一
 */
var checkReusableListNameUnique = function (reusableList, name) {
    for (var key in reusableList) {
        var isRepeat = reusableList[key].name === name;
        if (isRepeat) {
            return false;
        }
    }
    return true;
};
export { checkReusableListNameUnique };
