import { isHtmlElement } from './typeCheck';
var textListToTxt = function (textList) {
    var list = textList.list;
    var html = '';
    list.map(function (item) {
        if (isHtmlElement(item)) {
            html += item.data.html;
        }
    });
    var div = document.createElement('div');
    div.innerHTML = html;
    return div.innerText;
};
export { textListToTxt };
